@use '../../scss' as *;

.box {

    .box__img {
        position: relative;
        width: 100%;
        aspect-ratio: 1/0.75;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        .hover {
            position: absolute;
            inset: 0;
            background-color: #00000040;
            display: flex;
            flex-flow: column;
            gap: 1rem;
            align-items: center;
            justify-content: center;
            opacity: 0;

            .hover__item {
                text-decoration: none;
                cursor: pointer;
                background-color: #00000040;
                color: #fff;
                width: clamp(3rem ,10vw, 4rem);
                height: clamp(3rem ,10vw, 4rem);
                border-radius: 50%;
                display: flex;
                align-items: center;
                justify-content: center;

                > * {
                    font-size: clamp(1.5rem, 5vw, 2rem);
                    background-color: transparent;
                }
            }

        }

    }

    .box__name {
        padding: 1rem 0;
        text-transform: capitalize;
        font-weight: 500;
        letter-spacing: 0.05em;
        margin-block-start: 0.25rem;
    }

}